import fs from 'fs';
import { UniformServerConfig } from './UniformServerConfig';

export function getTempDir(uniformConfig: UniformServerConfig) {
    let tempDir = uniformConfig.UNIFORM_PUBLISH_TEMP_DIR;

    while (tempDir.endsWith('/') || tempDir.endsWith('\\')) {
        tempDir = tempDir.substr(0, tempDir.length - 1);
    }

    if (!fs.existsSync(tempDir)) {
        fs.mkdirSync(tempDir, { recursive: true });
    }

    return tempDir;
}
